*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  background-color: #2f3135;
}
/*# sourceMappingURL=index.css.map */
.App {
  display: flex;
}

.App .login .center {
  background-color: #2f3135;
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;
}

.App .login .center button {
  padding: 0.4rem 2rem;
  color: white;
  background-color: #738adb;
  font-weight: 700;
  outline-width: 0;
  font-size: 2rem;
}

.chat {
  height: 100vh;
  display: flex;
  flex: 0.75 1;
  flex-direction: column;
  color: grey;
}

.chat .chatHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6rem;
  border-bottom: 5px solid #383737;
}

.chat .chatHeader h1 {
  color: white;
}

.chat .chatHeader h1 span {
  margin-left: 3rem;
  margin-right: 1rem;
  font-size: 3rem;
  color: grey;
}

.chat .chatHeader .iconsDiv {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.chat .chatHeader .iconsDiv .originalIcon {
  margin: 0.4rem;
}

.chat .chatHeader .iconsDiv .chatHeaderSearch {
  display: flex;
  align-items: center;
}

.chat .mainChatBody {
  flex: 1 1;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /* Firefox */
}

.chat .mainChatBody ::-webkit-scrollbar {
  display: none;
}

.chat .mainChatBody .chatMassage {
  margin: 1rem 2rem;
  display: flex;
  align-items: center;
}

.chat .mainChatBody .chatMassage .massageAvatar {
  margin: 0.5rem;
}

.chat .mainChatBody .chatMassage .massageChat {
  margin: 0 1rem 0 0;
}

.chat .sendingTools {
  margin: 3rem;
  background-color: rgba(56, 55, 55, 0.8);
  box-shadow: 0 0 9px #000000;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.chat .sendingTools .sendingToolsIcon {
  margin: 0.6rem;
}

.chat .sendingTools .chatSendMassage {
  flex: 1 1;
}

.chat .sendingTools .chatSendMassage form input {
  width: 100%;
  height: 2rem;
  padding: 0.2rem;
}
/*# sourceMappingURL=chat.css.map */
.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.25 1;
  height: 100vh;
}

.sidebar .sidebarTop {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem;
  border-bottom: 0.3rem solid #26282c;
}

.sidebar .sidebarChannel {
  flex: 1 1;
  color: grey;
}

.sidebar .sidebarChannel .sidebarChannelHeader {
  width: 98%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sidebar .sidebarChannel .sidebarChannelHeader .channelHeader {
  display: flex;
  align-items: center;
}

.sidebar .sidebarChannel .sidebarChannelHeader .addIcon:hover {
  color: white;
  cursor: pointer;
}

.sidebar .sidebarChannel .channelList {
  margin-top: 0.6rem;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /* Firefox */
}

.sidebar .sidebarChannel .channelList .channel {
  padding: 0.4rem;
}

.sidebar .sidebarChannel .channelList .channel h3 {
  display: flex;
  padding-left: 1rem;
  align-items: center;
  cursor: pointer;
}

.sidebar .sidebarChannel .channelList .channel:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

.sidebar .sidebarChannel .channelList .channel:hover h3 {
  color: white;
  transform: scale(1.08);
}

.sidebar .sidebarChannel .channelList .channel:hover h3 span {
  color: white;
  margin: 0.4rem;
}

.sidebar .sidebarVoice {
  border-top: 1px solid grey;
  display: flex;
  align-items: center;
  color: grey;
  padding: 0.6rem;
}

.sidebar .sidebarVoice .voiceIcon {
  color: #37af47;
  margin-right: 0.7rem;
}

.sidebar .sidebarVoice .voiceInfo {
  flex: 1 1;
}

.sidebar .sidebarVoice .voiceInfo h3 {
  color: #37af47;
}

.sidebar .sidebarVoice .rightIcons {
  display: flex;
  flex-direction: column;
}

.sidebar .sidebarVoice .rightIcons .MuiSvgIcon-root {
  margin: 0.3rem;
}

.sidebar .sidebarProfile {
  border-top: 1px solid grey;
  display: flex;
  align-items: center;
  color: grey;
  padding: 0.6rem;
}

.sidebar .sidebarProfile .avatar {
  margin-right: 0.7rem;
}

.sidebar .sidebarProfile .profileInfo {
  flex: 1 1;
}

.sidebar .sidebarProfile .profileInfo h3 {
  color: #37af47;
}

.sidebar .sidebarProfile .profileIcon {
  display: flex;
  flex-direction: column;
}

.sidebar .sidebarProfile .profileIcon .MuiSvgIcon-root {
  margin: 0.2rem;
}

::-webkit-scrollbar {
  display: none;
}
/*# sourceMappingURL=sidebar.css.map */
